@import url('https://fonts.googleapis.com/css2?family=Galada&family=Gowun+Batang&family=Poor+Story&family=Yeon+Sung&display=swap');

.enterPart {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 40vw;
    margin-top: 25vh;
}

.title {
    color: #453a3a;
    font-size: 50px;
    font-family: 'Galada', cursive;
}

.fromStyle {
    width: 30vw;
}

.header {
    position: absolute;
    top: 0;
    right: 0;
    margin: 15px 18px 0 0;
}

.inputBox {
    margin-top: 30px;
    text-align: center;
}

.login {
    height: 50px;
    width: 90%;
    padding-left: 20px;
    border-radius: 30px;
    border: 0px solid;
    box-shadow: 1px 1px 8px 1px #dadce0 inset;
}

.signIn {
    font-size: 15px;
    margin-top: 5px;
    margin-right: 10px;
    float: right;
    text-decoration: none !important;
    color: #191919;
}

/*로그인 했을 시 main 페이지*/
.enterPart {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 40vw;
    margin-top: 30vh;
}

.submitButton {
    height: 50px;
    width: 100%;
    margin-top: 50px;
    border: none;
    border-radius: 30px;
    box-shadow: rgba(0, 0, 0, .2) 0 1px 1px -1px, rgba(0, 0, 0, .14) 0 1px 5px 0, rgba(0, 0, 0, .12) 0 1px 5px 0;
    background: linear-gradient(106.37deg, #dfe8ff 29.63%, #f3c6f1 51.55%, #ffcfd1 90.85%);
    font-size: 20px;
    font-weight: 700;
    color: #453a3a;
}

.greeting {
    margin-top: 20px;
    font-size: 25px;
    font-family: 'GangwonEdu_OTFBoldA';
}

.enterButton {
    display: flex;
    height: 50px;
    width: 80%;
    margin-top: 50px;
    border: none;
    border-radius: 30px;
    box-shadow: rgba(0, 0, 0, .2) 0 1px 1px -1px, rgba(0, 0, 0, .14) 0 1px 5px 0, rgba(0, 0, 0, .12) 0 1px 5px 0;
    background: linear-gradient(106.37deg, #dfe8ff 29.63%, #f3c6f1 51.55%, #ffcfd1 90.85%);
    font-size: 20px;
    font-weight: 700;
    font-family: 'Gowun Batang', serif;
    color: #453a3a;
    justify-content: center;
    align-items: center;
    padding: 12px 0;
}


@media (max-width:740px) {
    .enterPart {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100vw;
        margin-top: 20vh;
    }

    .title {
        text-align: center;
    }

    .inputBox:nth-child(1) {
        margin-top: 10px;
    }

    .fromStyle {
        background-color: #ffffff;
        width: 80vw;
        padding: 20px;
        border-radius: 30px;

    }

    .submitButton {
        background: linear-gradient(106.37deg, #dfe8ff 29.63%, #f3c6f1 51.55%, #ffcfd1 90.85%);
    }

    /*로그인 했을 시 main 페이지*/
    .enterPart {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100vw;
        margin-top: 30vh;
    }

    .enterButton {
        background: linear-gradient(106.37deg, #fed6d8 29.63%, #fccbf9 51.55%, #d1deff 90.85%);
    }

    .enterButton:hover {
        background: rgb(255, 251, 251);
        color: #453a3a;
    }

}